import { reactive, watch } from 'vue'
import pokemonApi from '@/services/pokemonApi'

const savedCart = localStorage.getItem('cart')
const initialState = savedCart ? JSON.parse(savedCart) : {
  items: [],
  total: 0,
  show: false
}

const state = reactive(initialState)

watch(() => state.items, (items) => {
  localStorage.setItem('cart', JSON.stringify({
    items,
    total: state.total,
    show: state.show
  }))
}, { deep: true })

export default {
  state,

  toggleCart() {
    state.show = !state.show
  },

  async addItem(item) {
    try {
      console.log('Adding item to cart:', item);
      
      const normalizedItem = {
        id: item.id,
        name: item.name,
        price: parseFloat(item.price) || 0,
        image_url: item.image_url, // Keep the original image URL
        set: item.set || '',
        rarity: item.rarity || '',
        quantity: 1,
        tcg_id: item.tcg_id
      };

      console.log('Normalized cart item:', normalizedItem);

      const existingItem = state.items.find(i => i.id === normalizedItem.id)
      if (existingItem) {
        existingItem.quantity++
      } else {
        state.items.push(normalizedItem)
      }

      this.calculateTotal()
      state.show = true
    } catch (error) {
      console.error('Error adding item to cart:', error);
      this.addItemFallback(item);
    }
  },

  addItemFallback(item) {
    // Use same image normalization here
    const imageUrl = item.image_url || 
                    item.imageUrl || 
                    (item.images && (item.images.large || item.images.small)) || 
                    '';

    const normalizedItem = {
      id: item.id,
      name: item.name,
      price: parseFloat(item.price) || 0,
      image_url: imageUrl, // Consistent property name
      set: item.set || '',
      rarity: item.rarity || '',
      quantity: 1
    };

    const existingItem = state.items.find(i => i.id === normalizedItem.id)
    if (existingItem) {
      existingItem.quantity++
    } else {
      state.items.push(normalizedItem)
    }

    this.calculateTotal()
    state.show = true
  },

  removeItem(itemId) {
    state.items = state.items.filter(item => item.id !== itemId)
    this.calculateTotal()
  },

  updateQuantity(itemId, quantity) {
    const item = state.items.find(i => i.id === itemId)
    if (item) {
      item.quantity = quantity
      this.calculateTotal()
    }
  },

  calculateTotal() {
    state.total = state.items.reduce((sum, item) => {
      return sum + (item.price * item.quantity)
    }, 0)
  },

  clear() {
    state.items = []
    state.total = 0
    state.show = false
  }
}
