import { reactive } from 'vue'

const state = reactive({
  collection: JSON.parse(localStorage.getItem('collection') || '[]'),
  wishlist: JSON.parse(localStorage.getItem('wishlist') || '[]')
})

export default {
  state,

  addToCollection(card) {
    if (!this.isInCollection(card.id)) {
      state.collection.push(card)
      this.saveCollection()
    }
  },

  removeFromCollection(cardId) {
    const index = state.collection.findIndex(card => card.id === cardId)
    if (index !== -1) {
      state.collection.splice(index, 1)
      this.saveCollection()
    }
  },

  addToWishlist(card) {
    if (!this.isInWishlist(card.id)) {
      state.wishlist.push(card)
      this.saveWishlist()
    }
  },

  removeFromWishlist(cardId) {
    const index = state.wishlist.findIndex(card => card.id === cardId)
    if (index !== -1) {
      state.wishlist.splice(index, 1)
      this.saveWishlist()
    }
  },

  isInCollection(cardId) {
    return state.collection.some(card => card.id === cardId)
  },

  isInWishlist(cardId) {
    return state.wishlist.some(card => card.id === cardId)
  },

  getCollection() {
    return state.collection
  },

  getWishlist() {
    return state.wishlist
  },

  saveCollection() {
    localStorage.setItem('collection', JSON.stringify(state.collection))
  },

  saveWishlist() {
    localStorage.setItem('wishlist', JSON.stringify(state.wishlist))
  }
}
